// #ifndef VUE3
import Vue from 'vue'
import App from './App'
import uviewPlus from 'uview-plus'
import './uni.promisify.adaptor'

Vue.config.productionTip = false
Vue.use(uviewPlus)

App.mpType = 'app'
const app = new Vue({
  ...App
})
app.$mount()
// #endif

// #ifdef VUE3
import { createSSRApp } from 'vue'
import App from './App.vue'
import uviewPlus from 'uview-plus'

export function createApp() {
  const app = createSSRApp(App)
  app.use(uviewPlus)
  return {
    app
  }
}
// #endif